<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        class="ma-2"
        v-bind="attrs"
        v-on="on"
        outlined
        small
        fab
        color="secondary"
      >
        <v-icon>mdi-delete-outline</v-icon>
      </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="text-h5">Delete Section</span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col>
              <v-card-subtitle id="item">Do you wont Delete </v-card-subtitle>
              <v-card-title id="item" v-text="sectionModel.name[lang]"></v-card-title>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" text @click="dialog = false"> Cancel </v-btn>
        <v-btn color="secondary" text @click="deleteSection">
          <span v-if="!progressDeleteSection">Delete</span>
          <v-progress-circular
            v-if="progressDeleteSection"
            color="primary"
            indeterminate
          ></v-progress-circular>
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { sectionController } from "./section";
export default {
  props: ["sectionData"],
    computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  data() {
    return {
      sectionModel: this.sectionData,
      dialog: false,
      progressDeleteSection: false,
    };
  },
  methods: {
    deleteSection: async function () {
      this.progressDeleteSection = true;
      await sectionController.deleteSection(this.sectionModel.id);
      this.progressDeleteSection = false;
      this.dialog = false;
      this.$emit("getVal");
    },
  },
};
</script>

<style>
#item {
  display: inline;
}
</style>